<template>
    <div>
        <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="moduleCode" SubModuleCode="subModuleCode"
            :IdentityColumn="primaryKeyforModule" :ListData="ListingData" :HeaderData="Headers" 
            :HeaderButtons="Buttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
            SearchPosition="top" :SearchFields="topsearchField" 
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"  v-on:LeftsearchButtonClick="leftCommonSearch"
            :IsFilterApplied="searchCondition ? true : false"  :LegendArray="[]" sourcetype=""
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" 
            v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
            <!-- v-on:ActionButtonClick="actionButtonClick" -->
            <!--  -->
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 's'">
                    <template v-if="data.row.s.Name != null">
                        <div>{{ data.row.s.Name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                
                <template v-if="data.column.COLUMN_NAME == 'start_time'">
                    <template v-if="data.row.start_time != null">
                        <div>{{ formatTime(data.row.start_time) }}</div>                        
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'end_time'">
                    <template v-if="data.row.start_time != null">
                        <div>{{ formatTime(data.row.end_time) }}</div>                        
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'shift_type'">
                    <template v-if="data.row.shift_type == 1" >
                        <div>Flexible</div>                       
                    </template>
                    <template v-if="data.row.shift_type == 0">
                        <div>Fixed</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'break_types'">
                    <template v-if="data.row.break_types == 1" >
                        <div>Paid</div>                       
                    </template>
                    <template v-if="data.row.break_types == 0">
                        <div>UnPaid</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'description'">
                    <template v-if="data.row.description != null">
                        <div>{{ data.row.description }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'from_user_id'">
                    <template v-if="data.row.from_user_id.Name != null">
                        <div>{{ data.row.from_user_id.Name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'to_user_id'">
                    <template v-if="data.row.to_user_id.Name != null">
                        <div>{{ data.row.to_user_id.Name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'is_skill_enable'">
                    <template v-if="data.row.is_skill_enable == 1">
                        <!-- <div>{{ data.row.to_user_id.Name }}</div> -->
                        <div class="fa fa-check text-success"></div>
                    </template>
                    <template v-else>
                        <div class="fa fa-check text-secondary"></div>
                    </template>
                </template>
                
                <template v-if="data.column.COLUMN_NAME == 'is_goal_enable'">
                    <template v-if="data.row.is_goal_enable == 1">
                        <!-- <div>{{ data.row.to_user_id.Name }}</div> -->
                        <div class="fa fa-check text-success"></div>
                    </template>
                    <template v-else>
                        <div class="fa fa-check text-secondary"></div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'is_feedback_enable'">
                    <template v-if="data.row.is_feedback_enable == 1">
                        <!-- <div>{{ data.row.to_user_id.Name }}</div> -->
                        <div class="fa fa-check text-success"></div>
                    </template>
                    <template v-else>
                        <div class="fa fa-check text-secondary"></div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'is_note_enable'">
                    <template v-if="data.row.is_note_enable == 1">
                        <!-- <div>{{ data.row.to_user_id.Name }}</div> -->
                        <div class="fa fa-check text-success"></div>
                    </template>
                    <template v-else>
                        <div class="fa fa-check text-secondary"></div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'is_training_feedback_enabled'">
                    <template v-if="data.row.is_training_feedback_enabled == 1">
                        <!-- <div>{{ data.row.to_user_id.Name }}</div> -->
                        <div class="fa fa-check text-success"></div>
                    </template>
                    <template v-else>
                        <div class="fa fa-check text-secondary"></div>
                    </template>
                </template>

                <!-- <template v-if="data.column.COLUMN_NAME == 'source_currency_id'">
                    <template v-if="data.row.source_currency_id.name != null">
                        <div>{{ data.row.source_currency_id.name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template> -->
                
                <!-- {{getStatus(data.row.status_id)}}{{ data.row[primaryKeyforModule] }} -->
                <template v-if="data.column.COLUMN_NAME == 'status_id'">                                
                    <listing-status :isfor="'COMPANYSETUP_UPDATE'" v-if="isCRMShowStausDrop(data.row.status_id, 'COMPANYSETUP_UPDATE')"
                        ref="updatestatus"  v-bind:dataid="data.row[primaryKeyforModule]" :data-controller="COMPANYSETUP_UPDATE"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.row[primaryKeyforModule]" data-action="index" :module=moduleCode :subModule=subModuleCode
                        v-bind:dataselected="getStatus(data.row.status_id) == '1001' ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                    </listing-status>
                </template>
            </template>
        </tg-list> 
        <AddCompanySetup :recordId="recordId" :title="$t('Manage ') + $t(subModuleCode)" v-if="showAddPopup" v-on:close="showAddPopup=false;recordId=0" v-on:addReloadData="FetchData(); showAddPopup=false;recordId=0" v-bind:moduleName=moduleCode v-bind:emitName="'addReloadData'" v-bind:addApiUrl=addAPIUrl v-bind:subModule=subModuleCode v-bind:addUserGuide=addUserGuide />
        
    </div>
</template>
<script>
