            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            // ReportID: null,
            // reportname: '',
            // reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            searchcondition: '',
            // MODULENAME: null,
            // SUBMODULECODE: null,
            PrimaryKeyId: null,
            privilegeParams: null,
            //addAPIUrl: '',
            sharetype: '',
            Headers: [],
            filteredHeaders: [],
            StatusListForListing: [
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
            Buttons:[
                {
                    title: 'Add',
                    iconClass: 'fa-plus',
                    callbackfunction: 'AddScreenPopup',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true
                }, {
                    id: 'ActiveMultiple',
                    title: 'Active',
                    iconClass: 'fa-check',
                    callbackfunction: 'ActiveMultipleListing',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: true
                }, {
                    id: 'InactiveMultiple',
                    title: 'Inactive',
                    iconClass: 'fa-close',
                    callbackfunction: 'InactiveMultipleListing',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: true
                }, {
                    id: 'DeleteMultiple',
                    title: 'Delete',
                    iconClass: 'fa-trash-o',
                    callbackfunction: 'commonDeleteData',
                    isdisabled: true,
                    isvisible: true,
                }    
            ],
            topsearchField : [
                {
                    fieldName: 'Search  ' ,
                    fieldType: "text",
                    // fieldIcon: "fa-file",
                    fieldSearchConditionName: '',
                    isSearch: true,
                    value: "",
                    listOptions: []
                },
            ]
        }
    },
    async created() {
        var ths = this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.privilegeParams = `controller=Break&action=`;
        await ths.FetchData();
        // Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'FOLDER_NAME')
        // Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'CREATED_BY')
        // Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'MODIFIED_BY')
         ths.getStatuslistForListing("Dynamic_Reporting", "en");
    },
    computed: {
        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
            const filteredItem = { ...item };

            // Check if USER_EMP_ID is null or empty, then replace with '--'
            if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                filteredItem.USER_EMP_ID = '--';
            }

            return filteredItem;
            });
        },
        
    },
    mounted() {
        var ths = this;
        ths.topsearchField.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },

    updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t(this.subModuleCode)

            divElement[0].appendChild(spanElement);
        }
    },

    methods: {
        getStatus: function(obj){
            try {
                const statusIdObj = JSON.parse(obj);
                return statusIdObj.value;
            } 
            catch (error) {
                console.error('Error parsing status_id:', error);
                return obj;
            }
        },

        formatTime(time) {
            if (time) {
                return moment(time).format('hh:mm A');
            } else {
                return '--';
            }
        },

