        commonDeleteData: function (event) {
            var vm = this;
            // CompanyDataService.CheckPrivilege(vm.privilegeParams+'Delete').then((response)=>{
            //     if(response.data){
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: vm.moduleCode,
                                subModuleCode: vm.subModuleCode,
                                refCode: "COMPANYSETUP_DELETE"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            CompanyDataService.CommonDeleteData(objectPostString).then(async function (response) {
                                if (response.status == 200) {
                                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                    vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                    vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                    if (vm.PageNumber <= 0) {
                                        vm.PageNumber = 1;
                                    }
                                    await vm.FetchData();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                }

                            },);
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }
            //     }
            //     else
            //     {
            //         vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
            //         return false;
            //     }
            // })
        },
        UpdateStatus: async function (status) {            
            var vm = this;
            if (status == 'Active') {
                status = 1001;
            } else {
                status = 1002;
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            
            if (val.length > 0) {
               
                vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    // Send IDs as a comma-separated string
                    var postJSON = {
                        ID: val,
                        StatusId: status,
                        IsFor: "COMPANYSETUP_UPDATE",
                        Module: vm.moduleCode,
                        Submodule: vm.subModuleCode,
                    };

                    var objectPostString = JSON.stringify(postJSON);
                    CompanyDataService.CommonUpdateStatus(objectPostString).then(response => {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    });
                });
            }
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        clickEventOfColumn: function (row, column) {
            // console.log("row",row,column.COLUMN_NAME)
            var vueObj  = this;

            if (column.COLUMN_NAME == 'title' && vueObj.moduleCode == "AnnouncementMessage") {
                this.recordId = row.id
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == 'title' && vueObj.moduleCode == "Review") {
                this.recordId = row.review_template_id 
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == 'title' && vueObj.moduleCode == "Skill") {
                this.recordId = row.skill_id 
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == 'stage_name' && vueObj.moduleCode == "Stage") {                
                this.recordId = row.stage_id 
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == vueObj.ActionColunName ) {
                
                this.recordId = row[vueObj.primaryKeyforModule]
                this.showAddPopup = true
            }                         
            
        },

        FetchData: async function () {
            var vueObj = this;
            this.isLoading = true;            
            var url=`CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${vueObj.searchCondition.trim()}&module=${vueObj.moduleCode}&submodule=${vueObj.subModuleCode}`;
            let serveUrl = vueObj.getServiceMethod(vueObj.moduleCode, vueObj.subModuleCode);
            await serveUrl(url)
                .then(function (response) {
                    
                    vueObj.Headers = response.data.FieldSchema;                                                           
                    vueObj.Headers.forEach((column) => {
                        column.DISPLAY_NAME = vueObj.$t(column.DISPLAY_NAME);
                        if (column.COLUMN_NAME == "status_id") {                            
                                    column.settings = {
                                    isInSlot: true
                                };
                            }

                        if (vueObj.moduleCode == "Shift" && column.COLUMN_NAME == "start_time") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }
                        if (vueObj.moduleCode == "Shift" && column.COLUMN_NAME == "end_time") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }
                        if (vueObj.moduleCode == "Shift" && column.COLUMN_NAME == "shift_type") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Break" && column.COLUMN_NAME == "break_types") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }
                        
                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_skill_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_goal_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_feedback_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_note_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_training_feedback_enabled") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (column.COLUMN_NAME == vueObj.ActionColunName) {
                            // vueObj.topsearchField.fieldSearchConditionName = column.COLUMN_NAME;
                            vueObj.topsearchField.map((item) => item.fieldSearchConditionName = [column.COLUMN_NAME]);
                            //column.DISPLAY_NAME = 'Status'
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter
                            };
                        }
                        
                        if(column.COLUMN_NAME == vueObj.primaryKeyforModule){
                            column.VISIBLE = false; 
                        }                       

                    });                             
                                       
                    if (response.data.Data != "" && response.data.Data != null) {
                        if (response.data.Data.length > 0) {
                            
                            vueObj.ListingData = response.data.Data;                            

                            vueObj.ListingData.forEach((item, index) => {

                                if(vueObj.moduleCode == "Department" && vueObj.subModuleCode == "Department"){
                                    item.is_visibile_to_client = item.is_visibile_to_client == 1 ? 'Yes' : 'No';
                                    //item.is_ticket_handler = item.is_ticket_handler == 1 ? 'Yes' : 'No';
                                }

                                else if(vueObj.moduleCode == "Category" && vueObj.subModuleCode == "Category"){
                                    item.category_type_id = JSON.parse(item.category_type_id).Name != null ? JSON.parse(item.category_type_id).Name : "--";
                                }

                                else if(vueObj.moduleCode == "ContactType" && vueObj.subModuleCode == "ContactType"){     
                                    item.type = JSON.parse(item.type).value == 0 ? "Email" :  JSON.parse(item.type).value == 1 ? "Url" : JSON.parse(item.type).value == 3 ? "Contact Number" : JSON.parse(item.type).value == 2 ? "None" : "--";
                                }                                 
                                
                                //else if(vueObj.moduleCode == "PaymentGateway" && vueObj.subModuleCode == "PaymentGateway"){                                                                  
                                    //item.payment_gateway_code = JSON.parse(item.payment_gateway_code).Name;
                                    //item.created_by = JSON.parse(item.created_by).Name != null ? JSON.parse(item.created_by).Name : "--";                                     
                                //}   

                                else if(vueObj.moduleCode == "ProvisionalRight" && vueObj.subModuleCode == "ProvisionalRight"){   
                                    vueObj.ListingData.forEach(item => {
                                        item.from_user_id = JSON.parse(item.from_user_id).Name != null ? JSON.parse(item.from_user_id).Name : "--";
                                        item.to_user_id = JSON.parse(item.to_user_id).Name != null ? JSON.parse(item.to_user_id).Name : "--";
                                    });                               
                                }                                 

                                else if(vueObj.moduleCode == "Stage" && vueObj.subModuleCode == "Stage"){                            
                                    item.phase_id = JSON.parse(item.phase_id).Name != null ? JSON.parse(item.phase_id).Name : "--";                                                              
                                } 
                                
                                else if(vueObj.moduleCode == "Status" && vueObj.subModuleCode == "Status"){                            
                                    item.status_type_id = JSON.parse(item.status_type_id).Name != null ? JSON.parse(item.status_type_id).Name : "--";                                                              
                                }  
                                
                                else if(vueObj.moduleCode == "User" && vueObj.subModuleCode == "UserTitle"){                            
                                    item.modified_by = JSON.parse(item.modified_by).Name != null ? JSON.parse(item.modified_by).Name : "--";                                                              
                                }  

                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Feedback"){                            
                                    item.type_code = JSON.parse(item.type_code).Name != null ? JSON.parse(item.type_code).Name : "--";                                                              
                                }  

                                else if(vueObj.moduleCode == "Location" && vueObj.subModuleCode == "Location"){                                                               
                                    item.location_country = JSON.parse(item.location_country).Name != null ? JSON.parse(item.location_country).Name : "--";   
                                    item.location_state = JSON.parse(item.location_state).Name != null ? JSON.parse(item.location_state).Name : "--";                                                           
                                } 
                                 
                                // else if(vueObj.moduleCode == "Break" && vueObj.subModuleCode == "Break"){  
                                //     item.break_types = item.break_types == "true" ? "Paid" : "Unpaid" ;                                                           
                                // }                                 
                                                                                    
                                item.created_by = JSON.parse(item.created_by).Name;
                                
                                
                            });
                            
                            vueObj.TotalRecords = vueObj.ListingData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                             
                        } else {
                            vueObj.ListingData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ListingData = [];
                        vueObj.TotalRecords = 0                        
                    }
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
            });
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                CompanyDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },

        getServiceMethod: function(moduleCode, subModuleCode){
            if(moduleCode == "AnnouncementMessage" && subModuleCode == "AnnouncementMessage"){
                return CompanyDataService.AnnouncementMessageListing;
            }
            else if(moduleCode == "Break" && subModuleCode == "Break"){
                return CompanyDataService.BreakListing;
            }

            else if(moduleCode == "Channel" && subModuleCode == "Channel"){
                return CompanyDataService.ChannelListing;
            }

            else if(moduleCode == "Category" && subModuleCode == "Category"){
                return CompanyDataService.CategoryListing;
            }

            else if(moduleCode == "ContactType" && subModuleCode == "ContactType"){
                return CompanyDataService.ContactTypeListing;
            }

            else if(moduleCode == "Department" && subModuleCode == "Department"){
                return CompanyDataService.DepartmentListing;
            }

            else if(moduleCode == "Designation" && subModuleCode == "Designation"){
                return CompanyDataService.DesignationListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Feedback"){
                return CompanyDataService.FeedbackListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Greeting"){
                return CompanyDataService.GreetingListing;
            }

            else if(moduleCode == "Immigration" && subModuleCode == "Passport"){
                return CompanyDataService.ImmigrationListing;
            }
            
            else if(moduleCode == "IndustryTypes" && subModuleCode == "IndustryTypes"){
                return CompanyDataService.IndustryTypeListing;
            }

            else if(moduleCode == "Location" && subModuleCode == "Location"){
                return CompanyDataService.LocationListing;
            }

            else if(moduleCode == "LoginAuthorization" && subModuleCode == "LoginAuthorization"){
                return CompanyDataService.LoginAuthorizationListing;
            }

            else if(moduleCode == "PaymentGateway" && subModuleCode == "PaymentGateway"){
                return CompanyDataService.PaymentGatewayListing;
            }

            else if(moduleCode == "ProvisionalRight" && subModuleCode == "ProvisionalRight"){
                return CompanyDataService.ProvisionalRightListing;
            }

            else if(moduleCode == "Review" && subModuleCode == "PerformanceReview"){
                return CompanyDataService.ReviewTemplateListing;
            }

            else if(moduleCode == "Shift" && subModuleCode == "Shift"){
                return CompanyDataService.ShiftListing;
            }

            else if(moduleCode == "Skill" && subModuleCode == "Skill"){
                return CompanyDataService.SkillListing;
            }

            else if(moduleCode == "Stage" && subModuleCode == "Stage"){
                return CompanyDataService.StageListing;
            }

            else if(moduleCode == "User" && subModuleCode == "UserTitle"){
                return CompanyDataService.UserTitleListing;
            }

            else if(moduleCode == "Status" && subModuleCode == "Status"){
                return CompanyDataService.StatusListing;
            }

            else if(moduleCode == "Appointment" && subModuleCode == "Service"){
                return CompanyDataService.ServiceListing;
            }
        }
    }
}
</script>